body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  width: 200px;
  pointer-events: none;
}

.LD-logo-header {
  padding: 2px;
  max-width: 95%;
}

.LD-logo {
  padding: 20px;
  max-width: 95%;
  max-height: 200px;
}

.heart {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 80%
}

.customer-logo {
  padding: 5px;
  max-width: 95%;
  max-height: 125px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 15px;
  border: 2px solid #000000;
  padding: 7px;
  background-color: #FFFFFF;
}


.toggle-logo {
  height: 260px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.main-page {
  background-color: #000000;
}

.black-header {
  background-color: #000000;
  padding: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  color: rgb(0, 0, 0);
}

.gray-app-header {
  background-color: #D4D4D4;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.purple-app-header {
  background-color: #A34FDE;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.blue-app-header {
  background-color: #364dd9;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.red-app-header {
  background-color: #e2345f;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.qr-wrapper {
  border-radius: 15px;
  border: 2px solid #000000;
  padding: 10px;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

